.gallery{

    height: calc(100vh);
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color:$GRAY_BACK_GALLERY ;

    // background: linear-gradient(
    //     to right top,
    //     $GRAY_BACK_GALLERY,
    //     white);
    h1{

        font-size: $TITLE1;
    }

    .grid_portefolio{

        display: grid;
        gap:20px 20px;
        grid-template-columns: 1fr 1fr 1fr;
        height: 50%;
        width: 50%;

        
    }

}